<template>
  <div class="container">
    <!-- Ï¢åÏ∏° Î©îÎâ¥ -->
    <aside class="sidebar">
      <h2 class="logo">üöá ÏÑúÏö∏ ÏßÄÌïòÏ≤† Í¥ÄÏ†ú</h2>
      <ul>
        <li
          v-for="item in menuItems"
          :key="item.name"
          :class="{ active: category === item.name }"
          @click="setCategory(item.name)"
        >
          {{ item.label }}
        </li>
      </ul>
    </aside>

    <!-- Î©îÏù∏ -->
    <main class="main">
      <header class="topbar">
        <h1>{{ topbarTitle }}</h1>
        <button @click="logout">Î°úÍ∑∏ÏïÑÏõÉ</button>
      </header>

      <!-- Ïã§ÏãúÍ∞Ñ ÏïåÎ¶º -->
      <section class="alerts" v-if="!category">
        <h3>üö® Ïã§ÏãúÍ∞Ñ Í≤ΩÍ≥† ÏïåÎ¶º</h3>
        <ul>
          <!-- Ïù¥Ï†ú filteredAlertsÎ•º ÏÇ¨Ïö©Ìï¥ ÌïÑÌÑ∞ÎßÅÎêú ÏïåÎ¶ºÎßå Î≥¥Ïó¨Ï§çÎãàÎã§ -->
          <li v-for="(alert, idx) in filteredAlerts" :key="idx">
            <span class="time">{{ formatTime(alert.alarm["@timestamp"]) }}</span>
            <span class="status">{{ alert.alarm["ÏÉÅÌÉú"] }}</span>
            <span class="location">{{ alert.alarm["ÏúÑÏπò"] }}</span>
            <span class="desc">
              [{{ alert.alarm["equipment_id"] }}]
              {{ alert.alarm["ÏãúÏÑ§"] }} - {{ alert.alarm["Ïù¥Î≤§Ìä∏"] }}
            </span>
          </li>
        </ul>
      </section>

      <!-- Kibana ÎåÄÏãúÎ≥¥Îìú -->
      <section class="dashboard" v-else>
        <iframe
          :src="dashboardUrl"
          frameborder="0"
          width="100%"
          height="100%"
        ></iframe>
      </section>
    </main>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userDepartment: null,
      category: null,
      alerts: [], // ÏõπÏÜåÏºìÏóêÏÑú Ïò§Îäî Î™®Îì† ÏïåÎ¶ºÏùÑ Ï†ÄÏû•
      socket: null,
      departmentMap: {
        // 'control' Ìï≠Î™©ÏùÄ Î°úÏßÅ Ï≤òÎ¶¨Î•º ÏúÑÌï¥ Î∞òÎìúÏãú ÌïÑÏöîÌï©ÎãàÎã§.
        'control': { label: 'Ï¢ÖÌï©', url: 'http://192.168.56.1:5601/goto/OVERALL_DASHBOARD_ID', team: null },
        'fire': { label: 'ÏÜåÎ∞©', url: 'http://192.168.56.1:5601/goto/2b6e15b0-5e1e-11f0-b853-af82cbb5aa36', team: 'fire_safety_team' },
        'electrical': { label: 'Ï†ÑÍ∏∞', url: 'http://192.168.56.1:5601/goto/3a5ba600-5e1e-11f0-b853-af82cbb5aa36', team: 'electrical_team' },
        'telecom': { label: 'ÌÜµÏã†', url: 'http://192.168.56.1:5601/goto/40bf6400-5e1e-11f0-b853-af82cbb5aa36', team: 'communication_team' },
      }
    };
  },
  computed: {
    // Î°úÍ∑∏Ïù∏Ìïú ÏÇ¨Ïö©ÏûêÏùò Î∂ÄÏÑúÏóê Îî∞Îùº Î©îÎâ¥Î•º ÎèôÏ†ÅÏúºÎ°ú ÏÉùÏÑ±
    menuItems() {
      const items = [{ name: null, label: "Ïã§ÏãúÍ∞Ñ ÏïåÎ¶º" }];
      
      if (this.userDepartment === 'control') {
        // 'control' ÏÇ¨Ïö©ÏûêÎäî 'Ï¢ÖÌï© Í¥ÄÏ†ú'Î•º Ï†úÏô∏Ìïú Î™®Îì† Î∂ÄÏÑúÏùò ÎåÄÏãúÎ≥¥Îìú Î©îÎâ¥Î•º Î¥ÖÎãàÎã§.
        Object.keys(this.departmentMap).forEach(deptKey => {
           if (deptKey !== 'control') { // 'control' Î©îÎâ¥Îäî Í±¥ÎÑàÎúÅÎãàÎã§.
             items.push({ name: deptKey, label: this.departmentMap[deptKey].label });
           }
        });
      } else if (this.departmentMap[this.userDepartment]) {
        // Îã§Î•∏ Î∂ÄÏÑú ÏÇ¨Ïö©ÏûêÎäî ÏûêÏã†Ïùò ÎåÄÏãúÎ≥¥Îìú Î©îÎâ¥Îßå Î¥ÖÎãàÎã§.
        const deptInfo = this.departmentMap[this.userDepartment];
        items.push({ name: this.userDepartment, label: deptInfo.label });
      }
      return items;
    },
    // Î∂ÄÏÑúÏóê Îî∞Îùº Ïã§ÏãúÍ∞Ñ ÏïåÎ¶ºÏùÑ ÌïÑÌÑ∞ÎßÅ
    filteredAlerts() {
      if (this.userDepartment === 'control') {
        return this.alerts;
      }
      const userTeam = this.departmentMap[this.userDepartment]?.team;
      if (userTeam) {
        return this.alerts.filter(alert => alert.alarm.team === userTeam);
      }
      return [];
    },
    topbarTitle() {
      if (this.category) {
        return this.departmentMap[this.category]?.label || "ÎåÄÏãúÎ≥¥Îìú";
      }
      return "Ïã§ÏãúÍ∞Ñ Í≤ΩÍ≥† ÏïåÎ¶º";
    },
    dashboardUrl() {
      return this.departmentMap[this.category]?.url;
    },
  },
  created() {
    // localStorageÏóêÏÑú Î∂ÄÏÑú Ï†ïÎ≥¥Î•º Í∞ÄÏ†∏Ïò¨ Îïå Í≥µÎ∞±ÏùÑ Ï†úÍ±∞Ìï©ÎãàÎã§.
    const department = localStorage.getItem('user_department')?.trim();
    
    // departmentMapÏóê Ìï¥Îãπ Î∂ÄÏÑú Ï†ïÎ≥¥Í∞Ä ÏûàÎäîÏßÄ ÌôïÏù∏Ìï©ÎãàÎã§.
    if (department && this.departmentMap[department]) {
      this.userDepartment = department;
      // 'control' ÌåÄÏùÄ Î°úÍ∑∏Ïù∏ ÌõÑ Í∏∞Î≥∏ÏúºÎ°ú 'Ïã§ÏãúÍ∞Ñ ÏïåÎ¶º'ÏùÑ Î≥¥Ïó¨Ï§çÎãàÎã§.
      // categoryÎ•º nullÎ°ú Ïú†ÏßÄÌï©ÎãàÎã§.
    } else {
      alert("Î∂ÄÏÑú Ï†ïÎ≥¥Í∞Ä Ïú†Ìö®ÌïòÏßÄ ÏïäÏäµÎãàÎã§. Îã§Ïãú Î°úÍ∑∏Ïù∏Ìï¥Ï£ºÏÑ∏Ïöî.");
      this.$router.push('/');
    }
  },
  mounted() {
    const socket = new WebSocket('ws://192.168.56.1:8001/ws');
    socket.onopen = () => console.log('‚úÖ WebSocket Ïó∞Í≤∞Îê®');
    socket.onmessage = (event) => {
      const parsed = JSON.parse(event.data);
      if (parsed["ÏÉÅÌÉú"] !== "Ïã¨Í∞Å" && parsed["ÏÉÅÌÉú"] !== "Í≤ΩÍ≥†") return;
      this.alerts.unshift({ alarm: parsed });
      if (this.alerts.length > 10) this.alerts.pop();
    };
    socket.onerror = (error) => console.error('‚ùå WebSocket ÏóêÎü¨:', error);
    socket.onclose = () => console.log('‚ùå WebSocket Îã´Ìûò');
  },
  methods: {
    setCategory(cat) {
      this.category = cat;
    },
    formatTime(timestamp) {
      const date = new Date(timestamp);
      return date.toLocaleString("ko-KR", { hour12: false });
    },
    logout() {
      localStorage.removeItem('user_department');
      this.$router.push('/');
    },
  },
};
</script>

