<template>
  <div class="login-page">
    <div class="login-box">
      <h2 class="logo">üöá ÏÑúÏö∏ ÏßÄÌïòÏ≤† Í¥ÄÏ†ú</h2>
      <div class="form-group">
        <input v-model="username" type="text" placeholder="ÏÇ¨Î≤à (8ÏûêÎ¶¨)" @keyup.enter="login"/>
      </div>
      <div class="form-group">
        <input v-model="password" type="password" placeholder="ÎπÑÎ∞ÄÎ≤àÌò∏" @keyup.enter="login"/>
      </div>
      <p v-if="error" class="error-message">{{ error }}</p>
      <button @click="login">Î°úÍ∑∏Ïù∏</button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'LoginView',
  data() {
    return {
      username: '',
      password: '',
      error: ''
    }
  },
  methods: {
    async login() {
      try {
        const res = await axios.post('http://192.168.56.1:5000/api/login', {
          username: this.username,
          password: this.password
        });

        // --- Ïó¨Í∏∞Î•º ÏàòÏ†ïÌïòÏÑ∏Ïöî! ---
        // Î°úÍ∑∏Ïù∏ ÏÑ±Í≥µ ÏùëÎãµÏóê 'department' Ï†ïÎ≥¥Í∞Ä ÏûàÎäîÏßÄ ÌôïÏù∏Ìï©ÎãàÎã§.
        if (res.status === 200 && res.data.department) {
          // Î∂ÄÏÑú Ï†ïÎ≥¥Î•º Î∏åÎùºÏö∞Ï†ÄÏùò localStorageÏóê Ï†ÄÏû•Ìï©ÎãàÎã§.
          localStorage.setItem('user_department', res.data.department);
          
          // ÎåÄÏãúÎ≥¥Îìú ÌéòÏù¥ÏßÄÎ°ú Ïù¥ÎèôÌï©ÎãàÎã§.
          this.$router.push('/dashboard');
        } else {
          // Î∂ÄÏÑú Ï†ïÎ≥¥Í∞Ä ÏóÜÎäî Í≤ΩÏö∞ ÏóêÎü¨ Ï≤òÎ¶¨
          this.error = "Î°úÍ∑∏Ïù∏Ïóê ÏÑ±Í≥µÌñàÏúºÎÇò Î∂ÄÏÑú Ï†ïÎ≥¥Í∞Ä ÏóÜÏäµÎãàÎã§.";
        }
      } catch (err) {
        if (err.response && err.response.data && err.response.data.message) {
          this.error = err.response.data.message;
        } else {
          this.error = 'Î°úÍ∑∏Ïù∏ Ïã§Ìå®. ÏÑúÎ≤Ñ ÎòêÎäî ÎÑ§Ìä∏ÏõåÌÅ¨ Ïó∞Í≤∞ÏùÑ ÌôïÏù∏ÌïòÏÑ∏Ïöî.';
        }
      }
    }
  }
}
</script>

