import Vue from 'vue'
import VueRouter from 'vue-router'
import LoginView from '@/LoginView.vue'
import Dashboard from '@/Dashboard.vue'

// Vue.use(VueRouter)Îäî main.jsÏóêÏÑú Ï≤òÎ¶¨ÌïòÎØÄÎ°ú Ïó¨Í∏∞Ïóî ÏóÜÏñ¥Ïïº Ìï©ÎãàÎã§.

const routes = [
  {
    path: '/',
    name: 'login',
    component: LoginView
  },
  {
    path: '/dashboard',
    name: 'dashboard',
    component: Dashboard
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
